import React, { useState, useEffect } from "react";
import { outlets, menuItems } from "./data";

const PRIMARY = "#553122";
const TERTIARY = "#f7f0e1";

const Pickup = () => {
    const [step, setStep] = useState(1);
    const [selectedOutlet, setSelectedOutlet] = useState(null);
    const [cart, setCart] = useState([]);
    const [category, setCategory] = useState("breakfast");
    const [selectedTime, setSelectedTime] = useState(null);
    const [orderNumber, setOrderNumber] = useState(null);
    const [orderStatus, setOrderStatus] = useState("preparing");

    // -----------------------------
    // Helpers
    // -----------------------------

    const addToCart = (item) => {
        if (!cart.find((c) => c.id === item.id)) {
            setCart([...cart, { ...item, quantity: 1 }]);
        }
    };

    const removeFromCart = (id) => {
        setCart(cart.filter((item) => item.id !== id));
    };

    const total = cart.reduce((sum, item) => sum + item.price, 0);

    const placeOrder = () => {
        const number = "APU" + Math.floor(Math.random() * 10000);
        setOrderNumber(number);
        setStep(4);
    };

    // Simulate Kitchen Updates
    useEffect(() => {
        if (step === 4) {
            setTimeout(() => setOrderStatus("almost-ready"), 5000);
            setTimeout(() => setOrderStatus("ready"), 10000);
        }
    }, [step]);

    const timeSlots = ["18:00", "18:20", "18:40", "19:00"];

    // -----------------------------
    // RENDER
    // -----------------------------

    return (
        <div className="min-h-screen p-6 mt-[120px]" style={{ backgroundColor: TERTIARY }}>
            {/* STEP 1 - OUTLET */}
            {step === 1 && (
                <>
                    <h2 className="text-3xl font-bold mb-6">Select Pickup Outlet</h2>

                    <div className="grid md:grid-cols-2 gap-6">
                        {outlets.map((outlet) => (
                            <div
                                key={outlet.id}
                                onClick={() => {
                                    setSelectedOutlet(outlet);
                                    setStep(2);
                                }}
                                className="bg-white p-6 rounded-lg shadow cursor-pointer hover:shadow-xl"
                            >
                                <h3 className="font-bold text-xl">{outlet.name}</h3>
                                <p>{outlet.address}</p>
                                <p>Ready in {outlet.eta}</p>
                                {outlet.busy && (
                                    <span className="text-red-500 font-semibold">BUSY</span>
                                )}
                            </div>
                        ))}
                    </div>
                </>
            )}

            {/* STEP 2 - MENU */}
            {step === 2 && (
                <>
                    <button onClick={() => setStep(1)} className="mb-4 underline">
                        Back
                    </button>

                    <h2 className="text-3xl font-bold mb-4">Choose Items</h2>

                    {/* Category Tabs */}
                    <div className="flex gap-4 mb-6">
                        {Object.keys(menuItems).map((cat) => (
                            <button
                                key={cat}
                                onClick={() => setCategory(cat)}
                                className={`px-4 py-2 rounded ${category === cat ? "text-white" : "border"
                                    }`}
                                style={{
                                    backgroundColor:
                                        category === cat ? PRIMARY : "white",
                                    borderColor: PRIMARY,
                                }}
                            >
                                {cat}
                            </button>
                        ))}
                    </div>

                    {/* Items */}
                    <div className="grid md:grid-cols-2 gap-6">
                        {menuItems[category].map((item) => (
                            <div key={item.id} className="bg-white p-4 rounded shadow">
                                <div className="text-3xl">{item.image}</div>
                                <h4 className="font-bold">{item.name}</h4>
                                <p>{item.desc}</p>
                                <p className="font-bold">‚Çπ{item.price}</p>

                                {!cart.find((c) => c.id === item.id) ? (
                                    <button
                                        onClick={() => addToCart(item)}
                                        className="mt-2 px-4 py-2 text-white rounded"
                                        style={{ backgroundColor: PRIMARY }}
                                    >
                                        Add
                                    </button>
                                ) : (
                                    <button
                                        onClick={() => removeFromCart(item.id)}
                                        className="mt-2 px-4 py-2 bg-red-500 text-white rounded"
                                    >
                                        Remove
                                    </button>
                                )}
                            </div>
                        ))}
                    </div>

                    {cart.length > 0 && (
                        <div className="mt-6 p-4 bg-white rounded shadow">
                            <p className="font-bold">Total: ‚Çπ{total}</p>
                            <button
                                onClick={() => setStep(3)}
                                className="mt-2 px-6 py-3 text-white rounded"
                                style={{ backgroundColor: PRIMARY }}
                            >
                                Continue
                            </button>
                        </div>
                    )}
                </>
            )}

            {/* STEP 3 - TIME */}
            {step === 3 && (
                <>
                    <button onClick={() => setStep(2)} className="mb-4 underline">
                        Back
                    </button>

                    <h2 className="text-3xl font-bold mb-6">Select Pickup Time</h2>

                    <div className="grid grid-cols-2 gap-4 mb-6">
                        {timeSlots.map((time) => (
                            <button
                                key={time}
                                onClick={() => setSelectedTime(time)}
                                className="p-3 border rounded"
                                style={{
                                    backgroundColor:
                                        selectedTime === time ? PRIMARY : "white",
                                    color: selectedTime === time ? "white" : PRIMARY,
                                    borderColor: PRIMARY,
                                }}
                            >
                                {time}
                            </button>
                        ))}
                    </div>

                    <button
                        disabled={!selectedTime}
                        onClick={placeOrder}
                        className="px-6 py-3 text-white rounded"
                        style={{
                            backgroundColor: PRIMARY,
                            opacity: selectedTime ? 1 : 0.5,
                        }}
                    >
                        Place Order
                    </button>
                </>
            )}

            {/* STEP 4 - CONFIRMATION */}
            {step === 4 && (
                <>
                    <h2 className="text-4xl text-green-600 font-bold mb-4">
                        Order Confirmed!
                    </h2>

                    <p className="text-xl mb-2">Order #{orderNumber}</p>
                    <p>{selectedOutlet?.name}</p>
                    <p>Pickup Time: {selectedTime}</p>
                    <p className="font-bold mt-4">Total: ‚Çπ{total}</p>

                    <div className="mt-6 p-4 bg-white rounded shadow">
                        <p className="font-semibold mb-2">Kitchen Status:</p>
                        <p>
                            {orderStatus === "preparing" && "üî• Preparing"}
                            {orderStatus === "almost-ready" && "‚è≥ Almost Ready"}
                            {orderStatus === "ready" && "‚úÖ Ready for Pickup"}
                        </p>
                    </div>
                </>
            )}
        </div>
    );
};

export default Pickup;